@import url(https://fonts.googleapis.com/css2?family=Francois+One&display=swap);
.cont {
    color: yellow;
}

.cont2 {
    color: red;
}

html{
    font-family: 'Francois One', sans-serif;
}

body{
    margin: 0;
    display: flex;
    flex-flow: column;
    height: 50%;
    overflow-y: hidden;

}

.container1{
    background-color: #2b1d61;
}

.container2{
    background-color: #b3b3b3;
}

.root{ 
    height: 100vh;
}

i {
    color: white;
    text-decoration: none;
}

.title{
    display: grid;
    grid-template-rows: 40px 30px;
    grid-row: 1;
    grid-column: 2/5;
    font-size: 45px;
    font-weight: 700;
    padding-top: 30px;
}

.subtitle{
    padding-top: 12px;
    padding-left: 4px;
    font-size: 20px;
    font-weight: 400;
    color: rgb(56, 56, 56);
}

.mainGrid1{
    z-index: 1;
    position: absolute;
    top: 70px;
    height: 100vh;
    width: 100vw;
    
    display: grid;
    grid-template-rows: repeat(10, 1fr);
    grid-template-columns: repeat(7, 1fr);
    grid-row-gap: 5px;
    row-gap: 5px;
}

.mainGrid2{
    z-index: 1;
    position: absolute;
    top: 70px;
    height: 100vh;
    width: 100vw;
    
    display: grid;
    grid-template-rows: repeat(10, 1fr);
    grid-template-columns: repeat(7, 1fr);
    grid-row-gap: 5px;
    row-gap: 5px;

}

.siteLogo{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column: 1;
}

.source{
    display: flex;
    align-items: center;
    justify-content: center;
    grid-column: 5;
}

@-webkit-keyframes reveal {
    from {opacity: 0;}
    to {opacity: 1}
}

@keyframes reveal {
    from {opacity: 0;}
    to {opacity: 1}
}

@-webkit-keyframes conceal {
    from {opacity: 1;}
    to {opacity: 0}
}

@keyframes conceal {
    from {opacity: 1;}
    to {opacity: 0}
}

.createSession{
    font-size: 19px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: white;
    grid-column: 2/7;
    grid-row: 4;
}

.publicSession{
    font-size: 19px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: white;
    grid-column: 2/7;
    grid-row: 5; 
}

.searchForSession{
    font-size: 19px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: white;
    grid-column: 2/7;
    grid-row: 6; 
}

.sessionPopupContainer1{
    z-index: 5;
    height: 100vh;
    width: 100vw;
    position:absolute;
    top: 0px;
}

.sessionPopupContainer2{
    z-index: -1;
    height: 100vh;
    width: 100vw;
    position:absolute;
    top: 0px;
}

.sessionPopup1{
    position: absolute;
    z-index: 6;
    top: 50%;
    left: 50%;
    height: 250px;
    width: 350px;
    opacity: 1;
    border-radius: 9px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: grid;
    grid-template-columns: 20px 1fr 20px;
    grid-template-rows: 20px 45px 45px 1fr 50px 1fr;
}

.sessionPopup2{
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 50%;
    height: 250px;
    width: 350px;
    opacity: 0;
    border-radius: 9px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: grid;
    grid-template-columns: 20px 1fr 20px;
    grid-template-rows: 20px 45px 45px 1fr 50px 1fr;
}

.playerCountText{
    display: flex;
    justify-content: center;
    align-items: center;
    grid-column: 2;
    grid-row: 2;
    color: white;
    font-size: 25px;
}

.playerCountContainer{
    color: white;
    grid-column: 2;
    grid-row: 3;
    display: grid;
    grid-template-columns: 70px 1fr 70px;
    grid-template-rows: 1fr;
    color: white;
    font-size: 32px;
    border-radius: 5px;
}

.playerCount{
    display: flex;
    justify-content: center;
    align-items: center;
}

.arrowLeft{
    display: flex;
    justify-content: center;
    align-items: center;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.arrowRight{
    display: flex;
    justify-content: center;
    align-items: center;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.confirm{
    display: flex;
    justify-content: center;
    align-items: center;
    grid-column: 2;
    grid-row: 5;
    font-size: 32px;
    border-radius: 5px;
}

.showPopup{
    z-index: 5;
    opacity: 1;
}

.blur{
    -webkit-filter: blur(5px) brightness(70%);
            filter: blur(5px) brightness(70%);
    transition: -webkit-filter 200ms;
    transition: filter 200ms;
    transition: filter 200ms, -webkit-filter 200ms;
}

.no-blur{
    transition: -webkit-filter 200ms;
    transition: filter 200ms;
    transition: filter 200ms, -webkit-filter 200ms;
}

.loadingIconContainer{
    grid-row: 5;
    grid-column: 4;
    display: flex;
    justify-content: center;
    align-items: center;  
}

@media only screen and (min-width: 700px){
    
}
.headerBar1{
    z-index: 2;
    position: fixed;
    width: 100%;
    display: grid;
    grid-template-columns: 90px 1fr 70px 100px 50px;
    grid-template-rows: 1fr;
    height: 70px;
    top: 0px;
    font-size: 22px;
    text-decoration: none;
    cursor: default;
    -webkit-filter: blur(5px) brightness(70%);
            filter: blur(5px) brightness(70%);
    transition: -webkit-filter 200ms;
    transition: filter 200ms;
    transition: filter 200ms, -webkit-filter 200ms;
}

.headerBar2{
    z-index: 2;
    position: fixed;
    width: 100%;
    display: grid;
    grid-template-columns: 90px 1fr 70px 100px 50px;
    grid-template-rows: 1fr;
    height: 70px;
    top: 0px;
    font-size: 22px;
    text-decoration: none;
    cursor: default;
    transition: -webkit-filter 200ms;
    transition: filter 200ms;
    transition: filter 200ms, -webkit-filter 200ms;
}

.placeholderLogo{
    display: flex;
    align-items: center;
    justify-content: center;
    grid-column: 2;
    font-size: 25px;
}

.help{
    display: flex;
    align-items: center;
    justify-content: center;
    grid-column: 3;
    cursor: pointer;
}

.settings{
    display: flex;
    align-items: center;
    justify-content: center;
    grid-column: 4;
    cursor: pointer;
}


.dropdownHelp1{
    font-size: 20px;
    padding-left: 30px;
    padding-right: 30px;
    border-radius: 5%;
    overflow-y: scroll;
    scrollbar-width: none;
    -ms-overflow-style: none;
    grid-row: 2;
    grid-column: 2;
    -webkit-animation: conceal 200ms forwards;
            animation: conceal 200ms forwards;
    transition: z-index 0s 200ms;
}

.dropdownHelp2{
    z-index: 2;
    font-size: 20px;
    padding-left: 30px;
    padding-right: 30px;
    border-radius: 5%;
    overflow-y: scroll;
    scrollbar-width: none;
    -ms-overflow-style: none;
    grid-row: 2;
    grid-column: 2;
    -webkit-animation: reveal 200ms forwards;
            animation: reveal 200ms forwards;
}

.dropdownHelp2::-webkit-scrollbar{
    display: none;
}

.helpContainer{
    position: relative;
    height: 100vh;
    width: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-rows: 1fr 75% 1fr;
    grid-template-columns: 1fr 75% 1fr;
}

.dropdownSettings1{
    font-size: 20px;
    width: 100%;
    height: 100%;
    grid-column: 2;
    grid-row: 2;
    display: grid;
    grid-template-rows: 1fr 5% 1fr 5% 1fr 5% 1fr;
    grid-template-columns: 1fr 75% 1fr;
    -webkit-animation: conceal 200ms forwards;
            animation: conceal 200ms forwards;
    transition: z-index 0s 200ms;
}

.dropdownSettings2{
    z-index: 2;
    font-size: 20px;
    width: 100%;
    height: 100%;
    grid-column: 2;
    grid-row: 2;
    display: grid;
    grid-template-rows: 1fr 5% 1fr 5% 1fr 5% 1fr;
    grid-template-columns: 1fr 75% 1fr;
    -webkit-animation: reveal 200ms forwards;
            animation: reveal 200ms forwards;
}

