    display: grid;
    grid-template-rows: 20% 1fr 20%;
    grid-template-columns: 1fr 75% 1fr;
}

.volume{
    color: white;
    font-size: 19px;
    background-color: #c079ff;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    grid-column: 2;
    grid-row: 1;
}

.textSize{
    color: white;
    font-size: 19px;
    background-color: #c079ff;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    grid-column: 2;
    grid-row: 3;
}

.darkMode{
    color: white;
    font-size: 19px;
    background-color: #c079ff;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    grid-column: 2;
    grid-row: 5;
}

.leaveSession{
    color: white;
    font-size: 19px;
    background-color: #c079ff;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
