    justify-content: center;
}

.placeholderArrowRight{
    background-color: rgb(0, 119, 255);
    grid-row: 2/5;
    grid-column:7;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.player2{
    grid-row: 1;
    grid-column: 1/11;
}
.player3{
    grid-row: 4;
    grid-column: 1/10;
}
.player4{
    grid-row: 7;
    grid-column: 1/10;
}

.gameCode{
    grid-row: 1;
    grid-column: 5/10;
}

.startButton{
    grid-row: 10;
